import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { map, shareReplay } from 'rxjs/operators';

import { RegisterModel, UserModel } from '../models';

import {
    environment
} from '../../../environments/environment';
import { ReplaySubject } from 'rxjs';

@Injectable()
export class AccountService {

    private currentUserSubject = new ReplaySubject<UserModel>(1);
    currentUser$ = this.currentUserSubject.asObservable();

    constructor(
        private http: HttpClient,
    ) { }

    getAll = (): Observable<Array<RegisterModel>> => this.http.get<Array<RegisterModel>>(environment.baseUrl);

    register(model: any) {
        return this.http.post(environment.baseUrl + 'account/register', model);
    }

    login = (email: String, password: String, rememberMe: Boolean) => {
        let user = {
            email: email,
            password: password,
            rememberMe: rememberMe
        }
        return this.http.post(environment.baseUrl + 'account/login', user)
            .pipe(
                shareReplay(),
                map((user: UserModel) => {
                    console.log(user);
                    this.currentUserSubject.next(user);
                    //TODO: use local storage for now, move to IndexedDB later
                    localStorage.setItem('user', JSON.stringify(user));
                })
            );

    }

    setCurrentUser(user: UserModel) {
        this.currentUserSubject.next(user);
    }


    logout() {
        return this.http.get<any>(environment.baseUrl + 'account/logout');
    }

}

