import { ValidatorFn, AbstractControl } from '@angular/forms';

export function checkDate(date: Date): ValidatorFn {
    return (control: AbstractControl) => {
        const year = new Date(control.value).getUTCFullYear();
        const month = new Date(control.value).getUTCMonth();
        const date2 = new Date(control.value).getUTCDate();

        if (date.getFullYear() === year) {
            if (date.getMonth() === month) {
                if (date.getDate() > date2) {
                    return { value: true };
                }
            } else if (date.getMonth() > month) {
                return { value: true }
            }
        } else if (date.getFullYear() > year) {
            return { value: true };
        }
        return null;
    };
}

export function mustMatch(firstText: AbstractControl): ValidatorFn {
    return (secondText: AbstractControl) => {
        if (!firstText && !secondText) {
            return null;
        }
        else if (secondText.hasError && !firstText.hasError) {
            return null;
        }
        else if (firstText.value !== secondText.value) {
            return { value: true };
        } else {
            return null;
        }
    }
}



