import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AccountService, IndexedDBService } from '../shared/services';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'GU-menu-nav',
  templateUrl: './menu-nav.component.html',
  styleUrls: ['./menu-nav.component.scss']
})

export default class MenuNavComponent implements OnInit {

  user: any;
  isLoggedIn = false;

  constructor(
    private router: Router,
    public accountService: AccountService,
    private indexedDBService: IndexedDBService
  ) { }

  async ngOnInit() {
    // Checking user with the server
    if (this.user) {
      await this.indexedDBService.deleteDBAsync();
    }
  }

  /**
   * Log user out,
   * delete database in browser,
   * then navigate user to login page.
   */
  logout = (): void => {
    this.accountService.logout().subscribe(
      () => {
        this.indexedDBService.deleteDBAsync();
        //  this.router.navigate(['/login']);
      },
      error => {
        console.log(error);
      }
    );
  }

}
