import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppComponent } from './app.component';
import { AgmCoreModule } from '@agm/core';

// pages
import { EventListComponent } from './pages/event-list/event-list.component';
import { EventEditComponent } from './pages/event-edit/event-edit.component';
import { HomeComponent } from './pages/home/home.component';
import { RegisterComponent } from './pages/register/register.component';
import { LoginComponent } from './pages/login/login.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';

// services
import { AuthGuard } from './shared/guard/auth.guard';
import {
  AccountService,
  EventService,
  IndexedDBService
} from './shared/services';
import MenuNavComponent from './menu-nav/menu-nav.component';
import { LayoutModule } from '@angular/cdk/layout';
import { GooglePlacesComponent } from './google-places.component';
import { EventDetailsComponent } from './pages/event-details/event-details.component';
import { ForgotPasswordComponent } from './pages/forgot-password/forgot-password.component';
import { ChangePasswordComponent } from './pages/change-password/change-password.component';
import { MaterialModule } from './modules/material.module';
import { TextErrorsComponent } from './errors/text-errors/text-errors.component';
import { ToastrModule } from 'ngx-toastr';
import { ErrorInterceptor } from './interceptors/error.interceptor';
import { NotFoundComponent } from './errors/not-found/not-found.component';
import { JwtInterceptor } from './interceptors/jwt.interceptor';
import { TextInputComponent } from './forms/text-input/text-input.component';
import { DateInputComponent } from './forms/date-input/date-input.component';
import { MemberEditComponent } from './members/member-edit/member-edit.component';

@NgModule({
  declarations: [
    AppComponent,
    EventListComponent,
    EventEditComponent,
    HomeComponent,
    RegisterComponent,
    LoginComponent,
    DashboardComponent,
    MenuNavComponent,
    GooglePlacesComponent,
    EventDetailsComponent,
    ForgotPasswordComponent,
    ChangePasswordComponent,
    TextErrorsComponent,
    NotFoundComponent,
    TextInputComponent,
    DateInputComponent,
    MemberEditComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    LayoutModule,
    MaterialModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCTAv20qgxgOCCMNMsMfWZaDmXDTJT2vfw'
    }), ToastrModule.forRoot({
      timeOut: 10000,
      positionClass: 'toast-top-right'
    })
  ],
  providers: [EventService, AccountService, AuthGuard, IndexedDBService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JwtInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
