import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { AccountService, IndexedDBService } from '../../shared/services';
import { HttpClient, HttpParams } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'GU-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent {

  isLoginError = false;
  isEmailError = false;
  hidePassword = true;
  errorMessage = '';
  rememberMe = false;
  isLoading = false;

  loginApiUrl = '/api/v1/account/login';
  getUserProfileUrl = '/api/v1/profile/getUserProfile';

  constructor(
    private router: Router,
    private accountService: AccountService,) { }

  gotoDashBoard(): void {
    this.router.navigate(['/dashboard']);
  }

  login(form: any): void {
    this.isLoading = true;

    this.accountService.login(form.email, form.password, form.rememberMe).subscribe(user => {
      this.isLoading = false;
      this.gotoDashBoard();
    },
      error => {
        this.isLoginError = true;
        this.isLoading = false;
        this.errorMessage = error.error.loginError;
      }
    );

  }

}
