import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AbstractControl, FormBuilder, FormGroup, ValidatorFn, Validators } from '@angular/forms';

import { AccountService } from 'src/app/shared/services';
import { ToastrService } from 'ngx-toastr';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'GU-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})

export class RegisterComponent implements OnInit {

  hidePassword = true;
  hideConfirmPassword = true;
  isLoading = false;
  registerForm: FormGroup;
  passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private fb: FormBuilder,
    private toastr: ToastrService) { }

  ngOnInit(): void {
    this.initializeForm();
  }

  initializeForm() {
    this.registerForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      confirmEmail: ['', [Validators.required, this.matchValues('email')]],
      password: ['', [Validators.required, Validators.pattern(this.passwordPattern)]],
      confirmPassword: ['', [Validators.required, this.matchValues('password')]],
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      gender: ['', Validators.required],
      DOB: ['', Validators.required]
    });
  }

  matchValues(matchTo: string): ValidatorFn {
    return (control: AbstractControl) => {
      return control?.value === control?.parent?.controls[matchTo].value
        ? null : { isMatching: true }
    }
  }

  register(): void {

    this.isLoading = true;

    this.registerForm.controls['DOB'].setValue(JSON.stringify(this.registerForm.controls['DOB'].value).replace(/"/g, ""));

    this.accountService.register(this.registerForm.value).subscribe(response => {
      console.log(response);
      // this.toastr.success(response.);
      this.router.navigateByUrl('login');
      this.isLoading = false;
    }, error => {
      console.log(error.error);
      this.isLoading = false;
    })
    this.isLoading = false;
  }
}
