import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EventModel } from '../models/event.model';
import {
  environment
} from '../../../environments/environment';

@Injectable()
export class EventService {

  constructor(private http: HttpClient) { }

  getAll = () => this.http.get(environment.baseUrl);

  getByDistance = (latitude: number, longitude: number, distance: number) => {
    const data = `?latitude=${latitude}&longitude=${longitude}&distance=${distance}`;
    return this.http.get(environment.baseUrl + data);
  }

  getByCity = (city: string, state: string) => {
    const data = `?city=${city}&state=${state}`;
    return this.http.get(environment.baseUrl + data);
  }

  getStates = () => this.http.get(environment.baseUrl);

  getCities = (state: string) => {
    const data = `?state=${state}`;
    return this.http.get(environment.baseUrl + data);
  }

  getEvent = (id: string) => this.http.get(environment.baseUrl + id);

  put = (event: EventModel) => this.http.post(environment.baseUrl, event);

  addParticipant = (data: any) => this.http.post(environment.baseUrl, data);

  remove = (id: number) => this.http.delete(`/${id}`);
}
