import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'GU-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})

export class DashboardComponent implements OnInit {

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  displayedColumns: string[] = ['name', 'numParticipants', 'startDate', 'startTime'];
  user: any;
  dataSource: any;
  dataSource2: any;

  constructor(private router: Router) { }

  ngOnInit(): void {
    this.dataSource = new MatTableDataSource<Events>(this.user.eventsCreated);
    this.dataSource.paginator = this.paginator;
    this.dataSource2 = new MatTableDataSource<Events>(this.user.eventsParticipating);
    this.dataSource2.paginator = this.paginator;
  }


  selectEvent(event: any) {
    this.router.navigate(['/event-edit']);
  }

}

export interface Events {
  name: string;
  numParticipants: number;
  startDate: Date;
  startTime: string;
}
