import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { EventService } from '../../shared/services';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'GU-event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.scss']
})

export class EventListComponent implements OnInit {

  events: any;
  userLocation: string;

  errorMessage = '';
  distanceError: string;
  cityError: string;

  distance: number;
  states: any;
  state: string;
  cities: any;
  city: string;

  latitude: number;
  longitude: number;
  mapType = 'roadmap';

  markers = [];

  constructor(
    private eventService: EventService,
    private router: Router
  ) { }

  ngOnInit() {
    this.eventService.getStates().subscribe(
      (result: any) => this.states = result,
      (error: string) => this.errorMessage = error
    );
  }

  getEventsByLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position) => {
        if (this.distance) {
          this.latitude = position.coords.latitude;
          this.longitude = position.coords.longitude;

          this.eventService.getByDistance(position.coords.latitude, position.coords.longitude, this.distance).subscribe(
            (result: { startDate: any; }[]) => {
              console.log(typeof result[0].startDate);
              this.events = result;
              this.events.length === 0 ?
                this.errorMessage = 'Can\'t find any event.' :
                this.errorMessage = '';
              this.addMarkers();
            },
            (error: string) => this.errorMessage = error
          );
        }
      });
    } else {
      this.userLocation = 'Geolocation is not supported by this browser';
    }
  }

  getEventsByCity() {
    if (!this.state) {
      this.cityError = 'Please select a state.';
    } else if (!this.city) {
      this.cityError = 'Please select a city.';
    } else {
      this.eventService.getByCity(this.city, this.state).subscribe(
        (result: any) => {
          this.events = result;
          this.events.length === 0 ?
            this.errorMessage = 'Can\'t find any event.' :
            this.errorMessage = '';
          this.addMarkers();
        },
        (error: string) => this.errorMessage = error
      );
    }
  }

  addMarkers() {
    this.markers = [];
    this.events.forEach((event: { location: { latitude: any; longitude: any; }; name: any; }) => {
      this.markers.push({
        lat: event.location.latitude,
        lng: event.location.longitude,
        label: event.name
      });
    });
  }

  // Get all the cities that have events
  getEventsCities() {
    this.eventService.getCities(this.state).subscribe(
      (result: any) => this.cities = result,
      (error: string) => this.errorMessage = error
    );
  }

  sortByDate() {
    console.log(this.events);
    this.events.forEach(() => {

    });
  }

}
